<template>
  <div id="plan">
    <div class="container-card">
      <div class="header">表单名称:  <el-input v-model="formAlias"></el-input></div>
      <!-- 上半部分 -->
      <el-row>
        <!-- 左半边 -->
        <el-col :span="14">
          <el-col :span="9">
            <el-table
              @select="rowclick"
              height="150"
              :data="tableData"
              border
              style="width: 245px"
            >
              <el-table-column type="selection" width="55"> </el-table-column>
              <el-table-column prop="index" label="序号" width="70">
              </el-table-column>
              <el-table-column prop="name" label="设计稿名称" width="119">
              </el-table-column>
            </el-table>
          </el-col>
          <el-col class="header_left" :span="4">
            <el-row
              ><el-upload
                action="https://jsonplaceholder.typicode.com/posts/"
                multiple
                :on-change="uploadfile"
                :show-file-list="false"
                :auto-upload="false"
                ><el-button type="primary" style="width: 60"
                  >上传设计稿</el-button
                ></el-upload
              ></el-row
            >
            <el-row
              ><el-button style="width: 60" type="danger" @click="deletefile"
                >删除设计稿</el-button
              ></el-row
            >
          </el-col>
        </el-col>
        <!-- 右半边 -->
        <el-col class="header_right" :span="9">
          <el-button @click="insert">添加</el-button>
          <el-button type="danger" @click="remove">删除</el-button>
          <!-- <el-button type="primary">复制</el-button> -->
        </el-col>
      </el-row>
      <!-- 中间部分 -->
      <div class="middle">
        <el-table
          @select="rowclick"
          border
          :data="tableData"
          style="width: 100%"
          height="350"
          :header-cell-class-name="starAdd"
        >
          >
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column
            label="序号"
            type="index"
            width="55"
          ></el-table-column>
          <el-table-column
            v-for="item in tableList1"
            :label="item.label"
            :width="item.width"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="照片" width="200">
            <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-table-column>
          <el-table-column
            v-for="item in tableList2"
            :label="item.label"
            :width="item.width"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 底部 -->
      <div class="footer">
        <el-checkbox v-model="checked" @change="checkChange"
          >不对外发布</el-checkbox
        >
        <el-button @click="cancel" type="info">取消</el-button>
        <el-button @click="submitDialogDisplay = true" type="primary"
          >提交</el-button
        >
      </div>
    </div>
    <!-- 提交对话框 -->
    <el-dialog width="30%" title="提交申请" :visible.sync="submitDialogDisplay">
      <span>将提交对当前页面的更改</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="info" @click="submitDialogDisplay = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 照片放大对话框 -->
    <el-dialog id="zoom" :visible.sync="visible">
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
    <!-- 取消对话框 -->
    <el-dialog width="30%" title="取消修改" :visible.sync="removeDisplay">
      <span>将取消当前页面的更改，所有未提交的信息将会丢失</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="info" @click="removeDisplay = false">取 消</el-button>
        <el-button type="primary" @click="toGroup">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  GetSingleSamplePlanBatch,
  UpdateBatchSamplePlans,
  DeleteBatchSamplePlans,
  AddBatchSamplePlans
} from "network/proofGroup.js";

import { arraySort } from "service/dateUtil.js";
import { Formdata, TableItem } from "service/Business/proofGroup.js";
export default {
  data() {
    return {
      submitDialogDisplay: false,
      checked: false,
      removeDisplay: false,
      tableData: [],
      tableList1: [
        { label: "样品号", prop: "sampleNubmber", width: "150" },
        { label: "业务员", prop: "salesMan", width: "150" },
        { label: "客户", prop: "customer", width: "150" },
        { label: "品名", prop: "sampleName", width: "150" },
      ],
      tableList2: [
        { label: "要求机种", prop: "machineType", width: "150" },
        { label: "要求针数", prop: "needleCount", width: "150" },
        {
          label: "要求用料(表纱(表纱混率)+里纱)",
          prop: "materialsRequired",
          width: "150",
        },
        { label: "要求尺寸(脚底*上统)", prop: "requiredZize", width: "150" },
        { label: "样品数量", prop: "sampleCount", width: "150" },
        { label: "颜色(主色)", prop: "mainColor", width: "150" },
        {
          label: "功能性、缝头方式、后道备注",
          prop: "featureSewingTypePostProcessComment",
          width: "150",
        },
        { label: "企划克重", prop: "planWeight", width: "150" },
        { label: "注意点", prop: "detectRequest", width: "150" },
        { label: "希望交期", prop: "planDeliveryTime", width: "150" },
        { label: "样品交期", prop: "sampleDeliveryTime", width: "150" },
        { label: "实际尺寸", prop: "actualSize", width: "150" },
        { label: "实际克重", prop: "sampleWight", width: "150" },
      ],
      /* 界面显示数据 */
      tableData1: [],
      middleDeleteData: [],
      dialogImageUrl: "",
      visible: false,
      formData: null,
      samplePlanStateBatch: 0,
      selectData: [],
      deleteData: [],
      emptyobj: {
        number: "",
        samplenumber: "",
        salesman: "",
        client: "",
        typename: "",
        picture: "",
        typemachine: "",
        needle: "",
        material: "",
        data: "",
        status: "",
        operation: true,
        size: "",
        weight: "",
      },
      id: 0,
      formAlias: "",
    };
  },
  props: {
    object2: {
      type: Array,
      default: [],
    },
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      //新建
      if (this.$route.query.iid === undefined) {
      }
      //编辑
      else {
        var data = this.$route.query.iid;
        GetSingleSamplePlanBatch(data).then((res) => {
          this.formData = res.result;
          const data = res.result.items[0];
          this.formAlias = data.alias;
          this.id = data.id;
          /* this.modelName = data.modelName; */
          this.samplePlanStateBatch = data.samplePlanStateBatch;
          this.checked = this.samplePlanStateBatch == 0;
          this.tableData = data.samplePlanBatch_Detail;
          this.addIndex(this.tableData);
        });
      }
    },
    //数据编号
    addIndex(data) {
      data.forEach((item, index) => {
        item.index = index;
      });
    },
    uploadfile(file, fileList) {
      this.tableData1 = fileList;
      for (var i = 0; i < this.tableData1.length; i++) {
        this.tableData1[i].index = i;
      }
    },
    //获得选中行数据
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //删除
    remove() {
      const data = this.selectData;
      //判断是否为空
      if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        //arr存放传给后台删除id，arr1存放界面删除的索引
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.id;
          arr1[index] = item.index;
        });
        //排序
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.tableData.splice(item, 1);
        });
        this.deleteData = arr;
        this.addIndex(this.tableData);
      }
    },
    //删除操作
    delete() {
      DeleteBatchSamplePlans(this.deleteData).then((res) => {
        console.log(res);
      });
    },
    //提交操作
    submit() {
      let formData = new Formdata(
          this.id,
          this.alias,
          this.samplePlanStateBatch,
          this.tableData
        );
        let arr = new Array();
        arr.push(formData);
      if (this.$route.query.iid === undefined) {
        AddBatchSamplePlans(arr).then(res=>{
          if(res.success){
             this.$message.success("提交成功！");
          }
        })
      } else {
        this.delete();
        UpdateBatchSamplePlans(arr).then((res) => {
          if (res.success) {
            this.$message.success("提交成功！");
          }
        });
      }
    },
    //取消
    cancel() {
      this.$router.go(-1);
    },
    deleteform() {
      for (var i = 0; i < this.middleDeleteData.length; i++) {
        this.tableData.splice(this.middleDeleteData[i].index, 1);
      }
      for (var i = 0; i < this.tableData.length; i++) {
        this.tableData[i].index = i;
      }
    },
    //添加
    insert() {
      const tableItem = new TableItem();
      if (this.selectData.length == 0) {
        this.tableData.push(tableItem);
      } else {
        let index = this.selectData[0].index;
        this.tableData.splice(index + 1, 0, tableItem);
      }
      this.addIndex(this.tableData);
    },
    //选框改变
    checkChange() {
      if (this.checked) {
        this.samplePlanStateBatch = 0;
      } else {
        this.samplePlanStateBatch = 1;
      }
    },
    deletefile() {
      for (var i = 0; i < this.tableData2.length; i++) {
        this.tableData1.splice(this.tableData2[i].index, 1);
      }
      for (var i = 0; i < this.tableData1.length; i++) {
        this.tableData1[i].index = i;
      }
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.visible = true;
      console.log(this.visible);
      /*       var x= document.getElementById("zoom");
      x.style.display = "block";
      console.log(x.style); */
    },
    toGroup() {
      window.location.reload();
    },
    starAdd(obj) {
      if (
        obj.columnIndex === 2 ||
        obj.columnIndex === 3 ||
        obj.columnIndex === 4 ||
        obj.columnIndex === 5 ||
        obj.columnIndex === 6
      ) {
        return "star";
      }
    },
  },
};
</script>

<style scoped>
.header {
  text-align: left;
  margin-bottom: 8px;
}
.header .el-input {
  width: 150px !important;
}
.header_right {
  text-align: right;
}
.header_right {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.header_left .el-row {
  margin-bottom: 10px;
}
.middle {
  margin: 8px 0;
}
.el-upload {
  width: 100%;
}
.footer {
  margin-top: 10px;
  text-align: right;
}
.footer .el-checkbox {
  margin: 0 30px;
}
.el-table th.gutter {
  display: table-cell !important;
}
</style>